import React from "react";
import { Link } from 'react-router-dom';
import './Content.css'


const items = [
    {
        "id": 0,
        "title": "Kontakt",
        "content": ["Sie können uns errreichen, indem Sie unser Kontaktformular ausfüllen. Dabei habel Sie freie Wahl, was Sie auswählen möchten. Es liegt ganz bei Ihnen.", 
                    "Zum erreichen des Kontaktformulars klicken Sie folgenden Link:", "Kontaktformular", 
                    "Nach dem Ausfüllen des Kontaktformulars erhalten wir Ihre Eingaben und können uns an die Arbeit machen.", 
                    "Daraufhin geht es mit der eigentlichen Erstellung der weiter."]
    },
    {
        "id": 1,
        "title": "Bezahlen",
        "content": ["Die Bezahlung erfolgt vor der Erstellung der Webseite, nach der Einigung beider Parteien den Vertrag gemeinsam abzuschließen. Sie können sich nach belieben den Plan auswählen, der zu Ihnen passt. Das heißt Sie haben die komplette Kontrolle auf ihr Produkt, worin Sie investieren möchten, für die Zukunft ihrer Firma. Falls noch Fragen offen sein sollten, können Sie uns gerne Kontaktieren."]
    },
    {
        "id": 2,
        "title": "Erstellung",
        "content": ["Bei der Erstellung brauchen Sie nichts Weiteres zu tun als ihre Vorlieben und Wünsche für ihre Webseite zu äußern, für alles andere sind wir zuständig. Das heißt Sie brauchen sich dabei keine Gedanken machen."]
    }
];

function Content ({selector}) {
    switch (selector.indexOf(true)) {
        case 0:
            return (
                <div className="text-display" style={{fontSize: '170%'}}>
                    <div className="innerBorder">
                    Sie können uns errreichen, indem Sie unser Kontaktformular ausfüllen. Dabei habel Sie freie Wahl, was Sie auswählen möchten.<br />Es liegt ganz bei Ihnen.<br /> <br />
                    <div style={{display: 'flex'}}>
                        <div style={{backgroundColor: 'rgb(255, 119, 51)', width: '0.2%', padding: '10px', borderRadius: '25px 0 0 25px'}}>
                        </div>
                        <div style={{backgroundColor: 'rgb(255, 221, 204)', width: '90%', padding: '10px', borderRadius: '0 25px 25px 0'}}>
                            Zum erreichen des Kontaktformulars klicken Sie folgenden Link:<br /><Link to="/kontaktformular" style={{color: 'black', fontWeight: 700}}>Kontaktformular</Link>
                        </div>
                    </div><br />
                    Nach dem Ausfüllen des Kontaktformulars erhalten wir Ihre Eingaben und können uns an die Arbeit machen.<br />
                    Daraufhin geht es mit der eigentlichen Erstellung der weiter.
                    </div>
                </div>
            );
        case 1:
            return (
                <div className="text-display" style={{fontSize: '170%'}}>
                    <div className="innerBorder">
                    Die Bezahlung erfolgt vor der Erstellung der Webseite, nach der Einigung beider Parteien den Vertrag gemeinsam abzuschließen.<br />
                    Sie können sich nach belieben den Plan auswählen, der zu Ihnen passt.<br />
                    Das heißt Sie haben die komplette Kontrolle auf ihr Produkt, worin Sie investieren möchten, für die Zukunft ihrer Firma.<br />
                    Falls noch Fragen offen sein sollten, können Sie uns gerne Kontaktieren.
                    </div>
                </div>
            );
        case 2:
            return (
                <div className="text-display" style={{fontSize: '170%'}}>
                    <div className="innerBorder">
                    Bei der Erstellung brauchen Sie nichts Weiteres zu tun als ihre Vorlieben und Wünsche für ihre Webseite zu äußern,
                    für alles andere sind wir zuständig. Das heißt Sie brauchen sich dabei keine Gedanken machen.
                    </div>
                </div>
            );
        default:
            break;
    }
}

export default Content