import { useEffect, useState } from 'react';
import Menu from './Menu';
import './Tabs.css'
import { produce } from 'immer';
import Content from './Content';




function Tabs () {
    const items = ["Kontakt", "Bezahlen", "Erstellung"];
    const [bg, setBg] = useState("contentTabs backgroundchange1");
    
    const [selector, setSelector] = useState(() => {
        const arr = new Array();
        arr.push(true);
        for (let i = 1; i < items.length; i++) {
            arr.push(false);
        }
        return arr;
    });

    function onSwitch (i) {
        setSelector((prevState) => {
            return produce(prevState, (draftState) => {
                for (let j = 0; j < draftState.length; j++) {
                    if (draftState[j] === true){
                        draftState[j] = false;
                        break;
                    }
                }
                draftState[i] = true;
            });
        })
    }

    useEffect (() => {
        setBg("contentTabs backgroundchange" + (selector.indexOf(true)+1));
    }, [selector]);

    return (
        <div className="whole">
            <div className='tabselector'>
                {items.map((e) => (
                    <Menu key={e} id={items.indexOf(e)} selector={selector[items.indexOf(e)]} content={e} onSwitch={onSwitch} />
                ))}
            </div>
            <div className={bg}>
                <div className='textContent'>
                    <Content selector={selector} />
                </div>
            </div>
        </div>
    );
}

export default Tabs;