import './Menu.css';
import { motion, AnimatePresence } from "framer-motion";

function Menu ({selector, content, onSwitch, id}) {
    return (
        <>
        <div
        onClick={() => onSwitch(id)}
        className='tab'>
            {selector ? <div className='selected'><p className='TabSteps'>{content}</p></div> : <div className='unselected'><p className='TabSteps'>{content}</p></div>}
            {selector ? <motion.div className="underline" layoutId="underline" /> : null}
        </div>
        </>
    );
}



export default Menu;